@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
html,
body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  background-color: #100e17;
  font-family: "Poppins", sans-serif;
  background: url(https://images.unsplash.com/photo-1456305951335-bb8134aeab8a);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

.card-list{margin-top:85px;display:flex;align-items:center;justify-content:space-evenly;flex-wrap:wrap}

.card-list__container{display:flex;flex-direction:column;justify-content:center;width:270px;background-color:#17141d;border-radius:10px;box-shadow:-1rem 0 3rem #000;-webkit-transition:0.4s ease-out;transition:0.4s ease-out;position:relative;left:0px}.card-list__container:not(:first-child){margin-left:-310px}.card-list__container:hover{box-shadow:0px 3px 33px -13px aqua;-webkit-transform:translateY(-30px) scale(1.06);transform:translateY(-30px) scale(1.06);-webkit-transition:0.4s ease-out;transition:0.4s ease-out}.card-list__container:hover .card-list__temperature{color:white;-webkit-transition:color 0.4s ease-out;transition:color 0.4s ease-out}.card-list__container:hover .card-list__heading{color:#838383;-webkit-transition:color 0.4s ease-out;transition:color 0.4s ease-out;text-shadow:2px 7px 5px rgba(27,27,27,0.6)}.card-list__container:hover .icon{-webkit-transform:scale(1.15);transform:scale(1.15);color:#21dddd;-webkit-transition:0.4s ease-out;transition:0.4s ease-out;-webkit-animation:shake 2s;animation:shake 2s}.card-list__container:hover .card-list__description{visibility:visible}.card-list__container:hover .card-list__date{color:#51a5a5;-webkit-transform:scale(1.2);transform:scale(1.2)}.card-list__container:hover ~ .card-list__container{position:relative;left:50px;-webkit-transition:0.4s ease-out;transition:0.4s ease-out}.card-list__heading{margin-top:40px;font-size:30px;color:#c5b8b8;text-shadow:2px 7px 5px rgba(14,14,14,0.863)}.card-list__temperature{color:#5f5f5f}.card-list__date{margin-top:3px;color:#456666;-webkit-transition:0.3s ease-out;transition:0.3s ease-out}.icon{font-size:75px;color:#237777}@-webkit-keyframes shake{0%{-webkit-transform:translate(1px, 1px) rotate(0deg);transform:translate(1px, 1px) rotate(0deg)}10%{-webkit-transform:translate(-1px, -2px) rotate(-1deg);transform:translate(-1px, -2px) rotate(-1deg)}30%{-webkit-transform:translate(3px, 2px) rotate(0deg);transform:translate(3px, 2px) rotate(0deg)}50%{-webkit-transform:translate(-1px, 2px) rotate(-1deg);transform:translate(-1px, 2px) rotate(-1deg)}70%{-webkit-transform:translate(3px, 1px) rotate(-1deg);transform:translate(3px, 1px) rotate(-1deg)}90%{-webkit-transform:translate(1px, 2px) rotate(0deg);transform:translate(1px, 2px) rotate(0deg)}}@keyframes shake{0%{-webkit-transform:translate(1px, 1px) rotate(0deg);transform:translate(1px, 1px) rotate(0deg)}10%{-webkit-transform:translate(-1px, -2px) rotate(-1deg);transform:translate(-1px, -2px) rotate(-1deg)}30%{-webkit-transform:translate(3px, 2px) rotate(0deg);transform:translate(3px, 2px) rotate(0deg)}50%{-webkit-transform:translate(-1px, 2px) rotate(-1deg);transform:translate(-1px, 2px) rotate(-1deg)}70%{-webkit-transform:translate(3px, 1px) rotate(-1deg);transform:translate(3px, 1px) rotate(-1deg)}90%{-webkit-transform:translate(1px, 2px) rotate(0deg);transform:translate(1px, 2px) rotate(0deg)}}.card-list__description{margin-bottom:15px;color:#4bd4d4;visibility:hidden;-webkit-backface-visibility:hidden;backface-visibility:hidden}

